@import "main";
// End Of Import

// .sideMenu

.overlay_main{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 91;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideMenu{
    position: fixed;
    top: 0;
    right: -291px;
    width: 290px;
    height: 100%;
    z-index: 92;
    background-color: #fff;
    transition: .5s;

    &.open{
        right: 0;
    }

    ul{

        li{
            text-align: start;
            padding: 10px 20px;

            a{
                display: flex;
                align-items: center;

                span{
                    margin-right: 10px;
                    font-size: 16px;
                    color: var(--main-color);
                }
            }
        }
    }
}

// .sideMenu

// Start Header
header{

    .top{
        padding: 35px 0;
        background-color: var(--main-color-light);

        .content{
            display: flex;
            align-items: center;
            justify-content: center;

            @include Sm{
                flex-direction: column;
            }

            .search{
                margin: auto;

                @include Sm{
                    width: 100%;
                    margin: 20px 0;
                }

                form{
                    display: flex;
                    align-items: center;
                    border-radius: 100px;
                    background-color: #fff;
                    height: 60px;

                    @include mob480{
                        flex-direction: column;
                        height: auto;
                        border-radius: 0;
                    }
                }

                .field{
                    position: relative;
                    height: 100%;
                    @extend .Flex;

                    @include Sm{
                        width: 100%;
                    }

                    @include mob480{
                        height: 50px;
                    }

                    input{
                        height: calc(100% - 20px);
                        width: 466px;
                        @include Sm{
                            width: 100%;
                        }
                        border: 0;
                        padding: 0 18px;
                        border-right: 1px solid #E6E6E6;
                        border-top-left-radius: 100px;
                        border-bottom-left-radius: 100px;
                        font-size: 16px;
                        font-family: bold;
                        color: var(--main-color);
                        
                        &::placeholder{
                            font-size: 16px;
                            font-family: bold;
                            color: #B8B8B8;
                        }
                    }

                    button{
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 10px;
                        width: 101px;
                        height: 40px;
                        @extend .Flex;
                        border-radius: 100px;
                        background-color: var(--sec-color);
                        border: 0;

                        span{
                            margin-right: 10px;
                            font-size: 16px;
                            color: var(--main-color);
                            font-family: bold;
                        }
                    }
                }


                .dropdown.bootstrap-select.bs3{
                    width: 150px;

                    @include Sm{
                        flex-shrink: 0;
                    }

                    @include mob480{
                        height: 50px;
                        width: 100%;
                    }

                    *{
                        outline: none !important;
                        box-shadow: none !important;
                    }

                    button{
                        background-color: transparent;
                        color: var(--main-color);
                        padding: 0;

                        @include mob480{
                            height: 100%;
                        }
                        
                        .filter-option{
                            padding: 0;
                            text-align: start;
                            padding-right: 20px;
                            flex-shrink: 0;
                            width: calc(100% - 20px);
                        }

                        .filter-option-inner,
                        .filter-option-inner-inner{

                            @include mob480{
                                height: 100%;
                                display: flex;
                                align-items: center;
                            }
                        }
                    }

                    .dropdown-toggle::after{
                        width: 16px;
                        height: 16px;
                        flex-shrink: 0;
                        border: none;
                        background: url("../images/FontAwsome (chevron-circle-down).png") no-repeat center;
                        background-size: cover;
                        margin-left: 0;
                        position: relative;
                        right: -10px;
                    }

                    .dropdown-menu .inner{
                        &::-webkit-scrollbar {
                            width: 5px;
                        }
                        &::-webkit-scrollbar-track {
                            background-color: var(--sec-color);
                        }
                        &::-webkit-scrollbar-thumb {
                            // border-radius: 10px;
                            background-color: var(--main-color);
                        }
                    }

                    .dropdown-menu ul{
                        text-align: start;
                        padding-right: 15px;
                        padding-left: 15px;
                        // min-height: 100px;
                    }
                }

                .dropdown.bootstrap-select.bs3.open .dropdown-menu,
                .dropdown.bootstrap-select.bs3.open .dropdown-menu ul{
                    display: block;
                }
            }

            .options{
                @include mob480{
                    display: flex;
                    align-items: center;
                }
                .menuTrigger{
                    @extend .Flex;
                    display: inline-flex;
                    flex-direction: column;
                    width: 60px;
                    height: 60px;
                    margin-left: 10px;
                    border-radius: 50%;
                    border: 1px solid var(--sec-color);
                    display: none;

                    @include Sm{
                        display: inline-flex;
                    }

                    span{
                        width: 30px;
                        height: 2px;
                        background-color: var(--sec-color);
                        display: block;
                        border-radius: 3px;

                        &:nth-child(2){
                            margin: 5px 0;
                        }
                    }
                }

                a{
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                }

                .call{
                    margin-left: 10px;
                }
            }
        }
    }

    .bottom{
        padding: 43px 0 148px;
        background-color: var(--main-color);

        @include Sm{
            display: none;
        }

        .content{
            

            ul{
                display: flex;
                justify-content: space-between;

                li{
                    &:not(:last-child){
                        a{
                            border-left: 1px solid rgba(255,255,255, .1);
                        }
                    }

                    a{
                        padding: 0 33px;
                        @include kh{
                            padding: 0 23px;
                        }
                        @extend .Flex;
                        flex-direction: column;

                        span{
                            margin-top: 8px;
                            font-size: 16px;
                            color: #fff;
                            transition: .5s;
                        }

                        &:hover{

                            span{
                                color: var(--sec-color);
                            }
                        }
                    }
                }
            }
        }
    }

    &.header_in{

        .bottom{
            padding-bottom: 43px;
        }
    }
}
// End Header

// Start Slider

.home_slider{
    margin-bottom: 60px;
    margin-top: -100px;
    position: relative;

    @include Sm{
        margin: 30px 0;
        margin-bottom: 100px;
    }

    .swiper-slide{
        position: relative;

        .content_bottom{
            position: absolute;
            bottom: 0;
            right: 0;
            width: 100%;
            padding: 0 25px 20px 14px;

            @include Sm{
                padding: 10px;
            }

            .category{
                padding: 0 10px;
                height: 35px;
                border-radius: 100px;
                background-color: var(--sec-color);
                display: inline-flex;
                align-items: center;

                span{
                    margin-right: 5px;
                    font-size: 12px;
                    color: var(--main-color);
                }
            }

            h2{
                font-size: 20px;
                font-family: bold;
                color: #fff;
                margin: 14px 0 17px;
                line-height: 30px;
                @include Sm{
                    font-size: 16px;
                    margin: 10px 0;
                }
            }

            > span{
                font-size: 12px;
                font-family: bold;
                color: var(--sec-color);
            }
        }
    }

    .arrows{
        position: absolute;
        top: 50%;
        right: 0;
        width: 100%;
        z-index: 9;

        @include mob480{
            top: 112%;
            width: 50%;
            right: 50%;
            transform: translateX(50%);
        }

        .container{
            position: relative;

            div{
                outline: none !important;

                @include mob480{
                    transform: scaleX(-1) translateX(0) !important;
                }

                &:after{
                    display: none;
                }
    
                &:first-child{
                    transform: translateX(62px) scaleX(-1);
                }

                &:last-child{
                    transform: translateX(-62px) scaleX(-1);
                }
            }
        }
    }
}

// End Slider

.right-categories{
    background-color: #fff;
    overflow: hidden;

    @include Sm{
        margin-bottom: 30px;
    }

    .head{
        height: 70px;
        padding: 0 30px;
        background-color: var(--main-color);
        @extend .Flex;
        justify-content: flex-start;

        span{
            margin-right: 19px;
            font-size: 25px;
            color: var(--sec-color);
            font-family: bold;
        }
    }

    ul{
        padding: 42px 30px 60px;
        background: url("../images/Mask Group 1.png") no-repeat bottom;
        background-size: cover;

        @include Sm{
            height: 300px;
            overflow-y: auto;
            background-size: cover;
        }

        @include mob480{
        }

        li{
            &:not(:last-child){
                margin-bottom: 35px;
            }
            a{
                display: flex;
                align-items: center;

                span{
                    margin-right: 19px;
                    font-family: bold;
                    color: #000;
                    font-size: 16px;
                    transition: .5s;
                }

                &:hover{

                    span{
                        color: var(--sec-color);
                    }
                }
            }
        }
    }
}

.head_kh{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 43px;

    @include Sm{
        align-items: baseline;
    }

    .right{
        display: flex;
        
        .box-l{
            margin-right: 17px;

            h2{
                font-size: 25px;
                font-family: bold;
                color: #000;
                margin-bottom: 8px;

                @include Sm{
