}

// Categories

.categories{

    .center_cat{
        background-color: #fff;
        padding: 30px 30px 0;

        .tabs_nav{

            .kh-over-mob {

                @include mob480{
                    overflow-x: auto;
                }
            }

            ul{
                display: flex;
                align-items: center;
                justify-content: space-between;

                @include mob480{
                    width: max-content;
                }
            }

            li{
                display: flex;
                align-items: center;
                cursor: pointer;
                padding: 0 18px;
                padding-bottom: 25px;
                @media (min-width: 1200px) and (max-width: 1399px){
                    padding: 0 8px;
                    padding-bottom: 25px;
                }
                border-bottom: 1px solid #E6E6E6;
                
                &.active{
                    border-bottom: 1px solid var(--sec-color);
                    
                    svg{

                        path{
                            fill: var(--main-color);
                        }
                    }

                    span{
                        color: var(--main-color);
                    }
                }

                svg{

                    path{
                        transition: .5s;
                    }
                }

                span{
                    font-size: 16px;
                    font-family: bold;
                    color: #B8B8B8;
                    margin-right: 8px;
                    transition: .5s;
                }

                &:hover{

                    span{
                        color: var(--main-color);
                    }

                    svg{
                        path{
                            fill: var(--main-color);
                        }
                    }
                }
            }
        }

        .divs{
            margin-top: 40px;

            @include Sm{
                margin-bottom: 30px;
            }

            .col-lg-6,
            .col-lg-4{

                a{

                    .pic{
                        position: relative;

                        svg{
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                        }
                    }

                    .details{

                        h2{
                            font-size: 16px;
                            color: #000;
                            line-height: 25px;
                            margin-bottom: 12px;
                            font-family: bold;
                            margin-top: 15px;
                        }

                        .dets{
                            display: flex;
                            align-items: center;

                            @include Sm{
                                text-align: center;
                                justify-content: center;
                            }

                            div{

                                &:first-child{
                                    margin-left: 20px;
                                }

                                span{
                                    margin-right: 7px;
                                    font-size: 12px;
                                    font-family: bold;
                                    color: var(--sec-color);
                                }
                            }
                        }
                    }
                }
            }

            .col-lg-4{
                a{

                    .pic{

                        img{
                            width: 100%;
                        }
                    }
                }
            }

            > div{
                display: none;
                
                &.active{
                    display: block !important;
                }

                a{
                    margin-bottom: 24px;
                    display: block;
                }
            }
        }
    }

    .cat-left{
        background-color: #fff;
        padding: 30px;

        #home_tabs_left{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 40px;

            li{
                display: flex;
                align-items: center;
                cursor: pointer;
                padding: 0 18px;
                width: 50%;
                border-bottom: 1px solid #E6E6E6;
                justify-content: center;
                padding-bottom: 30px;
                
                &.active{
                    border-bottom: 1px solid var(--sec-color);
                    svg{

                        path{
                            fill: var(--main-color);
                        }
                    }

                    span{
                        color: var(--main-color);
                    }
                }

                svg{

                    path{
                        transition: .5s;
                    }
                }

                span{
                    font-size: 16px;
                    font-family: bold;
                    color: #B8B8B8;
                    transition: .5s;
                }

                &:hover{

                    span{
                        color: var(--main-color);
                    }

                    svg{
                        path{
                            fill: var(--main-color);
                        }
                    }
                }
            }
        }

        .cat-left_divs{

            > div{
                display: none;

                &.active{
                    display: block;
                }
            }

            .box{
                display: flex;
                align-items: center;
                
                &:not(:last-child){
                    padding-bottom: 28px;
                    margin-bottom: 28px;
                    border-bottom: 1px solid #E6E6E6;
                }

                @include mob480{
                    flex-direction: column;
                }

                .right{
                    width: 140px;
                    height: 140px;
                    flex-shrink: 0;
                    position: relative;

                    svg{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }

                .left{
                    padding-right: 20px;

                    @include Sm{
                        padding-right: 0;
                        text-align: center;
                    }

                    h2{
                        font-size: 16px;
                        font-family: bold;
                        color: #000;
                        margin-bottom: 10px;

                        @include Sm{
                            text-align: start;
                        }

                        @include mob480{
                            text-align: center;
                            margin-top: 20px;
                        }
                    }
                    p{
                        font-size: 13px;
                        color: #B8B8B8;
                        line-height: 20px;
                        margin-bottom: 12px;
                    }

                    .details{

                        .dets{
                            display: flex;
                            align-items: center;

                            @include mob480{
                                justify-content: center;
                            }

                            div{

                                &:first-child{
                                    margin-left: 20px;
                                }

                                span{
                                    margin-right: 7px;
                                    font-size: 12px;
                                    font-family: bold;
                                    color: var(--sec-color);
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &.cat_single{
        margin: 60px 0;
        .head_kh{
            background-color: var(--main-color-light);
            padding: 17px;
            justify-content: center;
            margin-bottom: 0;

            .box-l{

                h2{
                    font-size: 25px;
                    font-family: bold;
                    color: var(--sec-color);
                }
            }
        }

        .center_cat{
            padding: 0;
            background-color: transparent;
        }

        .kh-over-mob{
            padding-top: 23px;
            background-color: #fff;
            ul{
                justify-content: center;

                li{

                    &:first-child{

                        &:hover{

                            svg path{
                                fill: transparent;
                                stroke: var(--main-color);
                            }
                        }
                        &.active{
                            svg path{
                                fill: transparent;
                                stroke: var(--main-color);
                            }
                        }
                    }
                }
            }
        }

        .divs{
            padding: 30px 30px 10px;
            background-color: #fff;

            .col-lg-4{

                .pic{

                    img{
                        height: 140px;
                        object-fit: cover;
                    }
                }
            }
        }
    }
    
}

