// .books_newsletters

.books_newsletters{
    margin: 60px 0;

    @include Sm{
        margin: 30px 0;
    }
    
    .content{
        background-color: #fff;
        padding: 33px 30px 83px;
        position: relative;
    }

    .swiper-slide{

        .cont{
            position: relative;
        }

        .overlay{
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(rgba(0, 0, 0, 0) 0%, #000 100%);
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            transition: .5s;
            opacity: 0;
            visibility: hidden;

            h3{
                font-size: 16px;
                font-family: bold;
                color: #fff;
                line-height: 25px;
                text-align: center;
                margin-bottom: 20px;
                transition: .5s;
                transition-delay: .2s;
                transform: translateY(20px);
                opacity: 0;
                visibility: hidden;
            }

            a{
                width: 94px;
                height: 35px;
                border-radius: 100px;
                margin-bottom: 22px;
                background-color: var(--sec-color);
                font-size: 12px;
                font-family: bold;
                color: var(--main-color);
                @extend .Flex;
                transition: .5s;
                transition-delay: .4s;
                transform: translateY(20px);
                opacity: 0;
                visibility: hidden;
            }
        }

        &:hover{

            .overlay{
                opacity: 1;
                visibility: visible;

                h3, a{
                    transform: none;
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }

    .swiper-pagination{
        bottom: 33px;
        right: 50%;
        transform: translateX(50%);

        span{
            margin: 0 4px;
            width: 33px;
            height: 3px;
            border-radius: 10px;
            background-color: #E6E6E6;
            outline: none;

            &.swiper-pagination-bullet-active{
                background-color: var(--sec-color);
            }
        }
    }
}

