// contact_page

.contact_page{
    margin: 60px 0;

    .col-lg-7{

        @include Sm{
            order: 1
        }

        .content{
            padding: 60px 80px;
            @include Xs{
                padding: 30px;
            }
            background-color: #fff;
            height: 100%;
            position: relative;

            &:after{
                content: "";
                display: block;
                width: 50px;
                height: 100%;
                background-color: #fff;
                position: absolute;
                top: 0;
                left: -30px;

                @include Sm{
                    display: none;
                }
            }
        }

        form{
            width: 100%;

            input{
                height: 50px;
                padding: 14px 52px;
                border: 1px solid #EEECEC;
            }
            
            textarea{
                padding: 14px 52px;
                border: 1px solid #EEECEC;
                height: 123px;
            }

            input,
            textarea{
                width: 100%;
                transition: .5s;

                &:hover,
                &:focus{
                    border-color: var(--main-color-light);
                    outline: none;
                }
            }

            .field{
                position: relative;

                &:not(:last-child){
                    margin-bottom: 16px;
                }

                svg{
                    position: absolute;
                    top: 13px;
                    right: 15px;
                    // transform: translateY(-50%);
                }
            }

            button{
                width: 100%;
                text-align: center;
                height: 50px;
                margin-top: 16px;
                background-color: var(--main-color-light);
                border: 0;
                font-size: 16px;
                font-family: bold;
                color: var(--sec-color);
                border: 1px solid transparent;
                transition: .5s;

                &:hover{
                    background-color: transparent;
                    color: var(--main-color-light);
                    border-color: var(--main-color-light);
                }
            }
        }
    }
}

